.quotePullBack{
    margin-left: -6px;
    }
.sketch{
    position: relative;
    margin: 0px auto;
    width: 48px;
    opacity: 0.8;
    }
.contentTopParagraph{
    padding-top: 0px;
    text-align: left;
    }
.halfPageRight {
    display: block;
    position: absolute;
    width: 100%;
    max-width: 1220px;
    margin: 0px auto;
    left: 0px;
    right: 0px;
    top: 48%;
    transform: translateY(-50%);
    }
.quoteSpaceLeft{
    padding-left: 12px;
    }
.specsBlock{
    position: relative;
    margin-top: 24px;
    }
.specsBlock .catagoryType{
    text-transform: uppercase;
    font-family: 'HCo Gotham SSm Medium';
    font-size: 12px;
    clear: both;
    }
.specsBlock .catagoryName{
    clear: both;
    margin: -6px 0px 6px 0px;
    }
