h1.darkBackground{
    /*color: #eabfba;*/
    /*color: rgb(255, 87, 166);*/
    color: #d8d2c2;
    text-shadow: 0px 0px 30px rgba(0, 0, 0, 1), 0px 0px 60px rgba(0, 0, 0, 1);
    }
h1.home{
    font-size: 29px;
    line-height: 1.15em;
    }
.landingPageTop{
    position: relative;
    top: 0px;
    margin-top: 0px;
    min-height: 100vh;
    max-height: 100vh;
    width: 100%;
    max-width: 100%;
    padding: 0px;
    }
.landingPageTopInner{
    position: relative;
    display: block;
    width: 100%;
    margin:0px auto !important;
    padding-left: 26px;
    padding-right: 26px;
    padding-top: 50% !important;
    transform: translateY(-10%);
    height: auto;
    }
.landingPageTopTextBlock{
    max-width: 200px;
    }
.landingBackground{
    background-image: url(../../images/mark-belford-home-1-xs.webp);
    background-position: center right;
    background-repeat: no-repeat;
    background-size: cover;
    }
.homeSectionBTF1{
    padding-top: 123px;
    padding-bottom: 99px;
    }
.butterfieldEightHeader{
    position: relative;
    margin: 0px auto;
    height: auto;
    width: max-content;
    padding: 0px;
    color: #fd1d86;
    }
.butterfieldEightHeader h1>*{
    position: relative;
    display: block;
    padding: 0px;
    line-height: 1em;
    }
.butterfieldEightHeader h1 .adj1{
    margin-top: 0px;
    left: 27px;
    font-size: 42px;
    }
.butterfieldEightHeader h1 .adj2{
    margin-top: -21px;
    font-size: 60px;
    }
.butterfieldEightHeader h1 .adj3{
    margin-top: -17px;
    left: 35px;
    font-size: 110px;
    }
.quotePullBack{
    margin-left: -6px;
    }
.sketch{
    position: relative;
    margin: 0px auto;
    width: 48px;
    opacity: 0.8;
    }
.contentTopParagraph{
    padding-top: 0px;
    text-align: center;
    }
.halfPageRight {
    display: block;
    position: absolute;
    width: 100%;
    /* background-color: #ecdfdb; */
    max-width: 1220px;
    margin: 0px auto;
    left: 0px;
    right: 0px;
    top: 48%;
    transform: translateY(-50%);
    }
@media only screen and (min-width:576px){
.landingPageTopTextBlock{
    max-width: 100%;
    }
}
@media only screen and (min-width:768px){
h1.home{
    font-size: 34px;
    }
.landingPageTopInner{
    padding: 0px 55px;
    }
.landingBackground{
    background-image: url(../../images/mark-belford-home-1.webp);
    background-position: center center;
    } 
}
@media only screen and (min-width:992px){
h1.home{
    font-size: 55px;
    line-height: 1.1em;
    }
.landingPageTopInner{
    max-width: 843px;
    padding-top: 50% !important;
    transform: translateY(-40%);
    }
}
@media only screen and (min-width:1200px){
h1.darkBackground{
    line-height: 1.02em;
    }
.quotePullBack{
    margin-left: -11px;
    }
.butterfieldEightHeader h1 .adj1{
    margin-top: 0px;
    font-size: 72px;
    left: 43px;
    }
.butterfieldEightHeader h1 .adj2{
    margin-top: -34px;
    left: 0px;
    font-size: 96px;
    }
.butterfieldEightHeader h1 .adj3{
    margin-top: -26px;
    left: 65px;
    font-size: 144px;
    }
.halfPageRight {
    display: block;
    position: absolute;
    width: 100%;
    /* background-color: #ecdfdb; */
    max-width: 1220px;
    margin: 0px auto;
