    transform: translateY(-50%);
    }    
}
@media only screen and (min-width:1550px){
.landingPageTopInner{
    max-width: 1364px;
    }
}
@media only screen and (min-width:1800px){
.landingPageTopInner{
    max-width: 1597px;
    }
}