.heroHeader{
    position: relative;
    margin: 0px auto;
    height: auto;
    width: min-content;
    padding: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    color: #fd1d86;
    }
.heroHeader h1>*{
    position: relative;
    display: block;
    padding: 0px !important;
    font-size: 96px;
    line-height: 1em;
    }
.heroHeader h1 .adj1{
    margin-top: 0px;
    margin-left: 27px;
    font-size: 42px;
    }
.heroHeader h1 .adj2{
    margin-top: -24px;
    font-size: 60px;
    }
.heroHeader h1 .adj3{
    margin-top: -18px;
    margin-left: 43px;
    font-size: 48px;
    }
.locationText{
    position: relative;
    left: 42px;
    bottom: 9px;
    }
.locationText .line1{
    position: relative;
    display: block;
    font-size: 18px;
    line-height: 18px;
    font-family: 'HCo Gotham SSm Book';
    }
.locationText .line2{
    font-size: 24px;
    text-transform: uppercase;
    font-family: 'HCo Gotham SSm Light'; 
    }
.swedishPrinceHeroPic{
    position: relative;
    z-index: -1;
    margin-left: -36px;
    margin-right: -36px;
    }
@media only screen and (min-width:1200px){
.heroHeader h1 .adj1{
    margin-top: 0px;
    margin-left: 121px;
    font-size: 42px;
    }
.heroHeader h1 .adj2{
    margin-top: -21px;
    font-size: 96px;
    }
.heroHeader h1 .adj3{
    margin-top: -28px;
    margin-left: 63px;
    font-size: 73px;
    }
.locationText{
    left: 66px;
    bottom: 15px;
    }
.swedishPrinceHeroPic{
    margin-top: -250px;
    margin-left: 0px;
    margin-right: 0px;
    }
}
